
html,
body,
div,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
p,
form,
input,
button,
a,
span,
i,
img {
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
}
a {
  display: inline-block;
  text-decoration: none;
  color: #000;
}
span {
  display: inline-block;
}
img {
  vertical-align: top;
}
i {
  font-style: inherit;
}
input,
button {
  border: none;
  outline: none;
  background: #fff;
}

.login-enter-active {
  -webkit-animation: bounce-in ease-in 0.5s;
          animation: bounce-in ease-in 0.5s;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
/* login css */
i {
  margin-bottom: 10px;
  width: 50px;
  height: 50px;
  position: relative;
  display: inline-block;
  color: #fff;
  font-size: 30px;
  background: #3fa8e6;
  border-radius: 50%;
}
i::before {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
i.icon-error {
  background: #d23b36;
}
i.icon-error::before {
  content: "\2718";
}
i.icon-success {
  background: #19be6b;
}
i.icon-success::before {
  content: "\2714";
}
i.icon-warn {
  background: #c7991d;
}
i.icon-warn::before {
  content: "!";
}
.login-box {
  width: 850px;
  height: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-box-shadow: 0 0 30px #ddd;
          box-shadow: 0 0 30px #ddd;
  background: #fff;
  border-radius: 3px;
}
.login-box .bgImg {
  width: 350px;
  height: 100%;
  float: left;
  position: relative;
  background: #bababa;
}
.login-box .bgImg img {
  width: 100%;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.form {
  padding-left: 350px;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.form .inner-box {
  padding: 50px;
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.form .inner-box[loading=true]::before {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  content: "\6CE8\518C\4E2D...";
  display: inline-block;
  color: #fff;
  visibility: visible;
  -webkit-transition: visibility ease 0.5s;
  transition: visibility ease 0.5s;
  text-align: center;
  line-height: 500px;
  background: rgba(0, 0, 0, 0.5);
}
.form .inner-box[loading=false]::before {
  visibility: hidden;
}
.form .inner-box .form-header {
  line-height: 40px;
  text-align: right;
  color: #666;
}
.form .inner-box .form-body {
  padding: 60px 60px 40px;
  min-height: 50%;
}
.form .inner-box .form-body .row-input {
  margin-bottom: 30px;
  width: 100%;
  height: 35px;
  position: relative;
}
.form .inner-box .form-body .row-input .icon-close {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  right: 6px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  display: inline-block;
  cursor: pointer;
  text-align: center;
  line-height: 13px;
  color: #666;
  border-radius: 50%;
  background: #ddd;
}
.form .inner-box .form-body .row-input input {
  padding-left: 5px;
  padding-right: 26px;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: border ease 0.5s, text-align ease 0.5s;
  transition: border ease 0.5s, text-align ease 0.5s;
  line-height: 35px;
  text-align: center;
  border-bottom: 1px solid #000;
}
.form .inner-box .form-body .row-input input:focus {
  text-align: left;
  border-left: 3px solid #3fa8e6;
  border-bottom: none;
}
.form .inner-box .form-body .row-input span {
  opacity: 0;
}
.form .inner-box .form-body .mistakeClasses input {
  border-color: #da3838;
}
.form .inner-box .form-body .mistakeClasses input:focus {
  border-color: #da3838;
}
.form .inner-box .form-body .mistakeClasses span {
  opacity: 1;
  font: 12px/30px "\5FAE\8F6F\96C5\9ED1";
  color: #da3838;
}
.form .inner-box .form-body .btn-row {
  text-align: right;
}
.form .inner-box .form-body .btn-row .btn {
  padding-right: 20px;
  display: inline-block;
  position: relative;
  cursor: pointer;
  -webkit-transition: padding ease 0.5s, color ease 0.5s;
  transition: padding ease 0.5s, color ease 0.5s;
  font-size: 15px;
  color: #555;
}
.form .inner-box .form-body .btn-row .btn:hover {
  padding-right: 18px;
  color: #000;
}
.form .inner-box .form-body .btn-row .btn::after {
  margin-top: -4px;
  width: 6px;
  height: 6px;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 5px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  content: "";
  border: 2px solid #3fa8e6;
  border-bottom: none;
  border-left: none;
  border-radius: 3px;
}
.form .register.inner-box .form-body {
  padding-top: 40px;
}
.form-footer .btn {
  position: relative;
  cursor: pointer;
  color: #666;
  -webkit-transition: border ease 0.5s, color ease 0.5s;
  transition: border ease 0.5s, color ease 0.5s;
  white-space: 1px;
}
.form-footer .btn:hover {
  color: #000;
}
.form-footer .btn::after {
  width: 0%;
  height: 1px;
  position: absolute;
  bottom: -2px;
  left: 0;
  display: inline-block;
  -webkit-transition: width ease 0.5s;
  transition: width ease 0.5s;
  content: "";
  background: #000;
}
.form-footer .btn:hover::after {
  width: 100%;
}
.pull-dialog {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  background: rgba(0, 0, 0, 0.6);
}
.pull-dialog .dialog-content {
  min-width: 300px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #fff;
  border-radius: 5px;
}
.pull-dialog .dialog-content .dialog-header {
  padding: 0 20px;
  line-height: 35px;
  color: #333;
  border-bottom: 1px solid #ddd;
}
.pull-dialog .dialog-content .dialog-body {
  font-size: 14px;
  text-align: center;
}
.pull-dialog .dialog-content .dialog-footer {
  padding: 8px 0;
  text-align: center;
  border-top: 1px solid #ddd;
}
.pull-dialog .dialog-content .dialog-footer button {
  padding: 5px 15px;
  min-width: 50px;
  cursor: pointer;
  line-height: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
.main .tips {
  margin-top: 100px;
  text-align: center;
}
.main .tips i {
  width: 80px;
  height: 80px;
  font-size: 45px;
}
.main .tips p {
  margin: 20px 0;
}
.main .tips .btn {
  padding: 8px 15px;
  cursor: pointer;
  border: 1px solid #999;
  border-radius: 5px;
}
.main .tips .btn:active {
  -webkit-box-shadow: 0 0 5px #919191;
          box-shadow: 0 0 5px #919191;
}
.main .loading {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  color: #fff;
  text-align: center;
  line-height: 120px;
  -webkit-transition: -webkit-transform ease-in 0.5;
  transition: -webkit-transform ease-in 0.5;
  transition: transform ease-in 0.5;
  transition: transform ease-in 0.5, -webkit-transform ease-in 0.5;
  background: rgba(0, 0, 0, 0.6);
}
